/* Import the Google Font */
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

/* Apply the font to a selector */
body {
  font-family: 'Open Sans', sans-serif;
}


.home{
    font-size: large;
    text-align: center;
    min-width: 100dvw;
    min-height: 100dvh;
    background-color: rgb(20, 20, 20);
}
.home p{
    color:rgb(189, 187, 187);
    padding:2px;
}

.home .hero{
    position: relative;
    top:33dvh;
}

.fade-in {
    opacity: 0;
    animation: fadeIn ease 2s; /* Adjust the animation duration as needed */
    animation-fill-mode: forwards;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateX(-20px); /* Optional: Add a subtle vertical slide effect */
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
